/**
 * Body Class scroll helper
 */
/* eslint-disable */
( function() {
  let prevScrollPos = window.pageYOffset;
  let scrollDirection = null;
  const bodyEl = document.body;
  const cssClassNames = { up: 'js-scroll-dir__up', down: 'js-scroll-dir__down', top: 'js-scroll-dir__top' };

  /**
   * onScroll body class helper
   */
  const onScroll = () => {
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

    if ( scrollTop < 40 ) {
      return;
    }
    if ( scrollTop > 40 ) {
      if ( Math.abs( prevScrollPos - window.pageYOffset ) > 10 ) {
        if ( prevScrollPos >= window.pageYOffset ) {
          if ( scrollDirection !== 'up' ) {
            scrollDirection = 'up';
            bodyEl.classList.remove( cssClassNames.down );
            bodyEl.classList.remove( cssClassNames.top );
            bodyEl.classList.add( cssClassNames.up );
          }
        } else if ( scrollDirection !== 'down' ) {
          scrollDirection = 'down';
          bodyEl.classList.remove( cssClassNames.up );
          bodyEl.classList.remove( cssClassNames.top );
          bodyEl.classList.add( cssClassNames.down );
        }
      }
    } else {
      bodyEl.classList.remove( cssClassNames.top );
      bodyEl.classList.remove( cssClassNames.up );
    }
    prevScrollPos = window.pageYOffset;
  };

  document.addEventListener( 'scroll', onScroll, { passive: true } );
}() );
