/*eslint-disable*/

const { React: { useState, useEffect }, React } = window;

export const Image = (
  {
    alt,
    imageSrc,
    onLoad = () => {
    },
    type = 'normal'
  },
) => {

  const [ loaded, setLoaded ] = useState( false );

  useEffect( () => {
    if ( loaded ) {
      onLoad();
    }
  }, [ loaded ] );

  const containerStyles = {};

  if ( 'bg' === type && loaded ) {
    containerStyles.backgroundImage = `url(${ imageSrc })`;
  }

  return (
    <div
      className={ `progressive-image${ loaded ? ' is-loaded' : '' }` }
      style={ containerStyles }
    >
      <img
        alt={ alt }
        src={ imageSrc }
        onLoad={ () => setLoaded( true ) }
        style={ { opacity: loaded && 'bg' === type ? 0 : 1 } }
      />
    </div>
  );
};

export default {
  Image,
};
