/*eslint-disable*/
import { useIntersectionObserver } from '../../hooks/useIntersectionObserver';
import { Image } from './Image';

const {
  React: {
    useState,
    useEffect
  },
  React
} = window;

export const ImageObserver = (
  {
    image = null,
    type = 'normal',
    alt = 'No Alt',
    onLoad = () => {},
  },
) => {
  const [ imageSrc, setImageSrc ] = useState( '' );
  const [ imageRef, isIntersecting ] = useIntersectionObserver( {} );

  useEffect( () => {
    if ( isIntersecting && '' === imageSrc ) {
      setImageSrc( image );
    }
  }, [ isIntersecting ] );

  if( null === image ){
    return null;
  }

  return (
    <>
      { isIntersecting ? <Image type={type} onLoad={onLoad} imageSrc={ imageSrc } alt={alt} /> : <div ref={ imageRef } className='progressive-image'/> }
    </>
  );
};

export default {
  ImageObserver,
};
